*{
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
}
nav{
    padding: 0px 200px;
    height: 80px;
    width: 100%;
}
.nav-bar{
    letter-spacing: 1.5px;
}
nav ul{
    float: right;
    margin-right: 20px;
}
nav ul li{
    display: inline-block;
    line-height: 80px;
    margin: 0 5px;
}
nav ul li a{
    color: gray;
    font-size: 20px;
    text-transform: uppercase;
    font-family: Arial, Helvetica, sans-serif;
}
nav ul li a:hover{
    cursor: pointer;
    color: #30df30;
}